$brk-base-colors: ();
$brk-base-colors: map-merge((
				"brand-primary":      adjust-hue(#2775FF, 120deg),
				"secondary":          adjust-hue(#7202bb, 120deg),
				"white":              #fff,
				"black":              #000,
				"brk-dark-base":      adjust-hue(#272727, 120deg),
				"brk-base-1":         adjust-hue(#69adff, 120deg),
				"brk-base-2":         adjust-hue(#00c6ff, 120deg),
				"brk-base-3":         adjust-hue(#0F5AE0, 120deg),
				"brk-base-4":         adjust-hue(#e8ecff, 120deg),
				"brk-base-5":         adjust-hue(#00f6ff, 120deg),
				"brk-base-6":         adjust-hue(#0B98F6, 120deg),
				"brk-base-7":         adjust-hue(#a7ebff, 120deg),
				"brk-base-8":         adjust-hue(#1600ba, 120deg),// TODO: !!!
				"brk-secondary-1":    adjust-hue(#4f34c9, 120deg),
				"brk-secondary-2":    adjust-hue(#4F00BA, 120deg),
				"brk-secondary-3":    adjust-hue(#7400ba, 120deg),
				"brk-secondary-4":    adjust-hue(#6e50c2, 120deg),
				"brk-secondary-5":    adjust-hue(#6377DF, 120deg),
				"brk-secondary-6":    adjust-hue(#b50aff, 120deg),
				"text-base-color-1":  #c4c4c4,
				"text-base-color-2":  adjust-hue(#0F5AE0, 120deg),
				"text-base-color-3":  #f3f3f3,
				"form-color-1":					adjust-hue(#e8ecff, 120deg),
), $brk-base-colors);

@function safe-rgb($color) {
	@return unquote("#{red($color)}, #{green($color)}, #{blue($color)}");
}

/* ----------- Palette variable ----------- */
:root {
	@each $color, $value in $brk-base-colors {
		--#{$color}: #{$value};
	}

	// RGB
	@each $color, $value in $brk-base-colors {
		--#{$color}-rgb: #{safe-rgb($value)};
	}

	--b-radius: inherit;
}
/* ----------- Palette variable ----------- */
@import "../../assets/styles/variables";
@import "../../assets/styles/mixins";


/*
// Title Styles
// ================ */
.highlight-massive {
	display: inline-block;
	position: relative;

	> .after {
		content: '';
		position: absolute;
		top: calc(100% - 4px);
		left: 0;
		width: 100%;
		height: 5px;
		background: var(--brand-primary);
		border-radius: 5px;
	}

	&-gray > .after {
		background: #E8E8E8;
		top: calc(100% - 1px);
		height: 3px;
	}
}

.highlight-trend {
	background: var(--brand-primary);
	border-radius: 50px;
	padding: 3px 20px;
	color: $white;
	display: inline-block;
	font-weight: 700;
	font-size: 14px;
	line-height: 24px;
	& + * {
		margin-top: 20px;
	}
}

.highlight-underline {
	position: relative;
	margin-top: 30px;
	text-transform: uppercase;
	padding: 6px 0 8px 40px;
	//font-size: 21px;
	//font-weight: 300;

	> .before {
		content: '';
		position: absolute;
		top: -1px;
		bottom: -1px;
		left: 0;
		width: 4px;
		border-radius: 10px;
		background: var(--brand-primary);
	}

	& + .highlight-underline {
		padding-top: 0;
		margin-top: -6px;
	}
}

.divider {
	width: 35px;
	height: 2px;
	background: var(--brand-primary);
	margin: 25px auto 32px;
	border: none;
}

.divider-light {
	width: 40px;
	height: 2px;
	background: #D4E1F6;
	margin: 20px auto 20px;
	border: none;
}

.divider-layers {
	width: 140px;
	height: 2px;
	background: #D4E1F6;
	margin: 30px auto 30px;
	border: none;
	position: relative;

	> .before {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		left: 50%;
		-webkit-transform: translate(-50%);
		transform: translate(-50%);
		background: var(--brand-primary);
		width: 40px;
	}
}

.divider-cross {
	width: 12px;
	height: 12px;
	margin: 18px auto 26px;
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: 6px;
		right: calc(100% + 15px);
		background: #E6E6E6;
		width: 65px;
		height: 1px;
	}

	&:after {
		content: '';
		position: absolute;
		top: 6px;
		left: calc(100% + 15px);
		background: #E6E6E6;
		width: 65px;
		height: 1px;
	}

	span {
		position: absolute;
		top: 50%;
		left: 50%;
		margin-left: -7px;
		width: 14px;
		height: 2px;
		background: var(--brand-primary);
		&:first-child {
			transform: rotate(45deg);
		}
		&:last-child {
			transform: rotate(-45deg);
		}
	}
}

.divider-lines {
	margin-top: 45px;
	span {
		display: block;
		width: 60px;
		margin: 0 auto;
		height: 1px;
		background: #272727;
		& + span {
			margin-top: 3px;
		}
	}
}

.divider-dots {
	margin: 20px auto 25px;
	line-height: 1;
	font-size: 0;
	span {
		display: inline-block;
		width: 5px;
		height: 5px;
		border-radius: 50%;
		background: var(--brand-primary);
		margin: 0 5px;
	}
}

.all-light {
	.title__heading-01 {
		&.title__heading-main {
			opacity: 0.15;
		}
		&.title__heading-sub {
			font-size: 28px;
		}
		&.divider {
			background: $white;
		}
	}
}

.title__heading {
	position: relative;
	&-01 {
		&.title__heading-main {
			opacity: 0.07;
			text-transform: uppercase;
			line-height: 1;
			@media(min-width: 768px) {
				font-size: 94px;
			}
			& + .title__heading-sub {
				margin-top: -40px;
			}
		}
		&.title__heading-sub {
			position: relative;
			z-index: 2;
			text-transform: uppercase;

		}
		&.divider {
			margin: 15px auto 0;
		}
	}
	&-02 {
		// margin-top: 110px;
		&.title__heading-main {
			text-transform: uppercase;
			position: relative;
			z-index: 1;
		}
		&.title__heading-sub {
			opacity: 1;
			color:rgba(34,34,34,0.08);
			transition: 1s ease color ;
			&.animated{
				 color: rgba(34, 34, 34, 0.8);
				 transition: .1s ease opacity;
			 }
			text-transform: uppercase;
			position: absolute;
			z-index: -1;
			top: 50%;
			left: 50%;
			-webkit-transform: translate(-50%, -50%);
			transform: translate(-50%, -50%);
			width: 100%;
			@media(min-width: 768px) {
				font-size: 94px;
			}
		}
	}
	&-03 {
		&.title__heading-main {
			text-transform: uppercase;
			line-height: 1;
			@media(min-width: 768px) {
				font-size: 48px;
			}
		}
		&.title__heading-sub {
			text-transform: uppercase;

		}
	}
	&-04 {
		&.title__heading-main {
			position: relative;
			z-index: 2;
		}
		&.title__heading-sub {
			opacity: 0.08;
			text-transform: uppercase;
			line-height: 1;
			@media(min-width: 768px) {
				font-size: 94px;
			}
			& + .title__heading-main {
				margin-top: -29px;
			}
		}
	}
	&-06 {
		&.title__heading-main {
			position: relative;
			z-index: 2;
			margin-top: -54px;
			text-transform: uppercase;
      line-height: 48px;
      &_light{
        color: #fff;
      }
		}
		&.title__heading-sub {
			color: #F6F6F6;
			text-transform: uppercase;
			@media(min-width: 992px) {
				font-size: 100px;
      }
      &_light{
        color: #fff;
        opacity: .08;
      }
      &_colored{
       color: rgba(var(--brand-primary-rgb), .08) 
      }
      &_dark{
        color: #e6e6e6;
      }
		}
	}
}

.heading-style-left {
	display: inline-block;
	position: relative;
	z-index: 2;
	text-align: left;

	h1,h2 {
		display: inline-block;
	}
	span {
		position: absolute;
		top: 100%;
		left: 0;
		color: #DDDDDD;
		text-transform: uppercase;
	}

	div + div {
		.highlight-underline {
			padding-top: 0;
			margin-top: -6px;
		}
	}
}

.title-lines {
	&-box {
		text-align: center;
		margin-top: 130px;
	}
	&-wrap {
		margin-top: 110px;
	}
	&-main {
		display: inline-block;
		padding: 0 40px;
		position: relative;
		@media(min-width: 768px) {
			padding: 0 80px;
		}
	}
	&-solid {
    display: flex;
    align-items: center;
    padding: 0;
    
		.text {
			position: relative;
			z-index: 1;
      padding: 0 20px;  
      flex-shrink: 1;
    }
    
    .line{
      flex-grow: 1;
      height: 5px;
      background: #EEF4FF;
    }
	}
	&-ridget {
		display: flex;
    align-items: center;
    
    padding: 0;
    
		.text {
			position: relative;
			z-index: 1;
			display: inline-block;
      padding: 0 20px;
      flex-shrink: 0;
    }
    
    .line{
      flex-grow: 1;

			height: 5px;
			background-image: url('#{$img}/heading-bg.png');
			background-position: center center;
      background-repeat: repeat-x;
      
    }
	}
	&-double {
    display: flex;
    align-items: center;
		padding: 0;
		.text {
			position: relative;
			z-index: 1;
      display: inline-flex;
      flex-shrink: 0;
			padding: 0 20px;
		}
		.line {
      flex-grow: 1;
			height: 5px;
			background: none;
      padding: 0;
      position: relative;
			&:before {
				content: '';
				position: absolute;
				top: 0;
				left: 0;
				width: 100%;
				height: 1px;
				background: #000000;
			}
			&:after {
				content: '';
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 1px;
				background: #000000;
			}
		}
	}
	&-bottom {
		line-height: 1;
		margin-top: 4px;
		&:after, &:before {
			content: '';
			position: absolute;
			bottom: 5px;
			right: 0;
			width: 47px;
			@media(min-width: 768px) {
				width: 88px;
			}
			height: 2px;
			background: $black;
		}
		&:before {
			right: auto;
			left: 0;
		}
	}
	&-four {
		position: relative;
		padding: 27px;
		border: 2px solid #EDEDED;
		span {
			position: absolute;
			width: 55px;
			height: 2px;
			background: #EDEDED;
			&:nth-child(1) {
				top: 0;
				left: 0;
				transform: rotate(45deg);
				transform-origin: top left;
			}
			&:nth-child(2) {
				top: 0;
				right: 0;
				transform: rotate(-45deg);
				transform-origin: top right;
			}
			&:nth-child(3) {
				bottom: 0;
				left: 0;
				transform: rotate(-45deg);
				transform-origin: bottom left;
			}
			&:nth-child(4) {
				bottom: 0;
				right: 0;
				transform: rotate(45deg);
				transform-origin: bottom right;
			}
		}
	}
	&-sota {
		display: inline-block;
		position: relative;
		z-index: 1;
		padding: 27px;
		@media(min-width: 768px) {
			padding: 27px 80px;
		}
		> .sota-1 {
			position: absolute;
			top: -10px;
			left: -15px;
			z-index: -1;
			.svg {
				position: absolute;
				top: 0;
				left: 0;
				&:nth-child(1) {
					left: 23px;
				}
				&:nth-child(2) {
					top: 15px;
				}
				&:nth-child(3) {
					top: 28px;
					left: 23px;
				}
				&:nth-child(4) {
					top: 45px;
					left: 75px;
				}
				&:nth-child(5) {
					top: 58px;
					left: 50px;
				}
			}
			svg {
				width: 34px;
				height: 38px;
				path {
					stroke-width: 3px;
					stroke: rgb(240, 240, 240);
				}
			}
		}
		> .sota-2 {
			position: absolute;
			top: 25px;
			right: -10px;
			z-index: -1;
			.svg {
				position: absolute;
				top: 0;
				right: 0;
				&:nth-child(1) {
					top: 15px;
					right: 0;
				}
				&:nth-child(2) {
					top: 0;
					right: 24px;
				}
				&:nth-child(3) {
					top: 30px;
					right: 24px;
				}
				&:nth-child(4) {
					top: 15px;
					right: 48px;
				}
				&:nth-child(5) {
					top: 0;
					right: 72px;
				}
			}
			svg {
				width: 34px;
				height: 38px;
				path {
					stroke-width: 3px;
					stroke: rgb(240, 240, 240);;
				}
			}
		}
  }
  &-dotted{
    position: relative;

    display: flex;
    align-items: center;

    .text{
      position: relative;
      z-index: 1;
      display: inline-flex;
      padding: 0 20px;
      
      flex-shrink: 0;
    }

    .line{
      flex-grow: 1;
      height: 12px;
			background-image: url(../../img/dotted-bg_wide.png);
      background-position: center center;
      background-repeat: repeat-x;
    }
    
  }
}

