		font-size: 16px;
	}
	.video-btn {
		position: relative;
		display: inline-block;
		p {
			margin-top: 10px;
			letter-spacing: 0.8px;
			@media(min-width: 480px) {
				margin-top: 0;
				position: absolute;
				left: calc(100% + 6px);
				top: 50%;
				transform: translate(0, -50%);
				white-space: nowrap;
			}
		}
	}
