}

.extra__heading-2 {
	text-align: left;
	max-width: 735px;
	padding: 106px 58px 0px;
	margin: 0 auto;
	border-right: 17px solid rgba($white, 0.1);
	border-bottom: 17px solid rgba($white, 0.1);
	position: relative;
	z-index: 1;
	p {
		line-height: 34px;
	}
	&:after {
		content: '';
		position: absolute;
		bottom: 0;
		left: 0;
		width: 17px;
		height: calc(100% - 90px);
		background: rgba($white, 0.1);
		z-index: -1;
	}
	&:before {
		content: '';
		position: absolute;
		top: 0;
		right: 0;
		height: 17px;
		width: calc(100% - 240px);
		background: rgba($white, 0.1);
		z-index: -1;
	}
	.heading {
		position: absolute;
		top: 0;
		left: 0;
		transform: translate(0px, -65px);
		@media(min-width: 768px) {
			transform: translate(-33px, -65px);
		}
		h1, h2 {
			line-height: 48px;
		}
	}
	.after-heading {
		display: none;
		position: absolute;
		top: 0;
		left: 0;
		transform: translate(13px, -105%);
		opacity: 0.1;
		font-size: 100px;
		text-transform: uppercase;
		@media(min-width: 768px) {
			display: block;
		}
	}
	.btn {
		min-width: 220px;
		color: #661DD9;
		font-size: 14px;
		border-radius: 7px;
		height: 55px;
		letter-spacing: 1.2px;
		margin-left: 0;
		margin-right: 0;
		margin-bottom: -35px;
		&.btn-inside-out-invert > .before, &.btn-inside-out-invert > .after {
			color: #661DD9;
		}
		&.btn-inside-out-invert > .before, &.btn-inside-out-invert > .after, &.btn-inside-out-invert > .text {
			padding-right: 15px;
		}
		.text {
			color: #661DD9;
		}
		.icon-left {
			position: absolute;
			top: 13px;
			right: 24px;
			font-size: 24px;
			color: #661DD9;
		}
  }
  &_primary{
      .btn {
      color: var(--brand-primary);
      &.btn-inside-out-invert > .before, &.btn-inside-out-invert > .after {
        color: var(--brand-primary);
      }
      .text {
        color: var(--brand-primary);
      }
      .icon-left {
        color: var(--brand-primary);
      }
    }
  }
}

